import { TextField } from '@mui/material'
import React from 'react'
import { UserData } from '../../pages/SettingsUser/SettingsUser'
import { useForm } from 'react-hook-form'
import customAxios from '../../axios'

export const UpdateUserForm = ({}) => {
	const {
		register,
		handleSubmit,
		setValue,
		formState: { errors },
	} = useForm<UserData>()

	const [loading, setLoading] = React.useState(true)

	React.useEffect(() => {
		const fetchMe = async () => {
			const { data } = await customAxios.get('auth/meinfo')

			setValue('name', data.name_user)
			setValue('fname', data.fname_user)
			setValue('oname', data.oname_user)
			setValue('email', data.email)
			setValue('phone', data.phone_number)
			setLoading(false)
		}
		fetchMe()
	}, [])

	const onSubmit = async (values: UserData) => {
		try {
			const { data } = await customAxios.patch('/auth/update', {
				...values,
			})

			alert(data.message)
			localStorage.removeItem('token')
			localStorage.setItem('token', data.token)
		} catch (error) {
			console.log(error)
			alert('   ')
		}
	}

	if (loading) {
		return <p>Loading...</p>
	}

	return (
		<form onSubmit={handleSubmit(onSubmit)}>
			<div className='form-block'>
				<h3 className='form-block__title'> </h3>
				<TextField
					id='outlined-basic'
					label=''
					className='form-block__input form-block__input--update'
					variant='outlined'
					{...register('name', { required: ' ' })}
				/>
				{errors.name && <p style={{ color: 'red' }}>{errors.name.message}</p>}
				<TextField
					id='outlined-basic'
					label=''
					className='form-block__input form-block__input--update'
					variant='outlined'
					{...register('fname', { required: ' ' })}
				/>
				{errors.fname && <p style={{ color: 'red' }}>{errors.fname.message}</p>}
				<TextField
					id='outlined-basic'
					label=''
					className='form-block__input form-block__input--update'
					variant='outlined'
					{...register('oname', { required: ' ' })}
				/>
				{errors.oname && <p style={{ color: 'red' }}>{errors.oname.message}</p>}
				<TextField
					id='outlined-basic'
					label='Email'
					className='form-block__input form-block__input--update'
					variant='outlined'
					{...register('email', { required: ' email' })}
				/>
				{errors.email && <p style={{ color: 'red' }}>{errors.email.message}</p>}
				<TextField
					id='outlined-basic'
					label=''
					className='form-block__input form-block__input--update'
					variant='outlined'
					{...register('phone', { required: '  ' })}
				/>
				{errors.phone && <p style={{ color: 'red' }}>{errors.phone.message}</p>}
				<button type='submit' className='button button--footer'>
					
				</button>
			</div>
		</form>
	)
}

export default UpdateUserForm
