import React from 'react'
import { useForm } from 'react-hook-form'
import { DataProduct } from '../../pages/AdminPanel/AdminPanel'
import { FormControl, InputLabel, MenuItem, Select, TextField } from '@mui/material'
import { createProduct } from '../../redux/slices/productSlice'
import customAxios from '../../axios'
import { useAppDispatch } from '../../redux/store'
import { arrCategories } from '../Categories/Categories'

export const AdminForm: React.FC<{ nameButton: string; productData?: DataProduct }> = ({
	nameButton,
}) => {
	const [imgUrl, setImgUrl] = React.useState('')
	const [category, setCategory] = React.useState('')
	const inputFileRef = React.useRef<HTMLInputElement>(null)
	const dispatch = useAppDispatch()

	const {
		register,
		handleSubmit,
		formState: { errors },
	} = useForm<DataProduct>()

	const onSubmit = async (values: DataProduct) => {
		try {
			const data = await dispatch(createProduct({ ...values, fileimg: imgUrl }))
			if (!data.payload) {
				return alert('   !')
			}
			if (data.payload) {
				window.location.reload()
			}
		} catch (error) {
			console.log(error)
		}
	}

	const handleFileChange = async (event: any) => {
		try {
			const formData = new FormData()
			formData.append('image', event.target.files[0])

			customAxios.post(`${process.env.REACT_APP_SERVER_URL}/upload`, formData).then(({ data }) => {
				setImgUrl(data.url)
			})
		} catch (error) {
			console.log(error)
		}
	}

	const deleteImg = () => {
		try {
			customAxios.delete(`${process.env.REACT_APP_SERVER_URL}/upload/delete/${imgUrl}`)
			if (inputFileRef.current) {
				inputFileRef.current.value = ''
				setImgUrl('')
			}
		} catch (error) {
			console.log(error)
		}
	}

	return (
		<form onSubmit={handleSubmit(onSubmit)}>
			<div className='form-block__inputs'>
				<TextField
					id='outlined-basic'
					className='form-block__input'
					label=''
					variant='outlined'
					{...register('title', { required: ' ' })}
				/>
				{errors.title && <p style={{ color: 'red' }}>{errors.title.message}</p>}
				<TextField
					placeholder=''
					multiline
					rows={7}
					maxRows={8}
					{...register('description', { required: ' ' })}
				/>
				{errors.description && <p style={{ color: 'red' }}>{errors.description.message}</p>}
				<TextField
					id='outlined-basic'
					label=''
					className='form-block__input'
					variant='outlined'
					{...register('price', { required: ' ' })}
				/>
				{errors.price && <p style={{ color: 'red' }}>{errors.price.message}</p>}
				<FormControl fullWidth>
					<InputLabel id='demo-simple-select-label'></InputLabel>
					<Select
						labelId='demo-simple-select-label'
						id='demo-simple-select'
						value={category}
						label=''
						{...register('category', {
							required: ' ',
							onChange: (event) => setCategory(event.target.value),
						})}
					>
						{arrCategories.slice(1).map((item, index) => (
							<MenuItem key={index + 1} value={index + 1}>
								{item}
							</MenuItem>
						))}
					</Select>
				</FormControl>
				{errors.category && <p style={{ color: 'red' }}>{errors.category.message}</p>}
				<TextField
					id='outlined-basic'
					label='  1  5'
					className='form-block__input'
					variant='outlined'
					{...register('rating', { required: ' ' })}
				/>
				{errors.rating && <p style={{ color: 'red' }}>{errors.rating.message}</p>}
				<TextField
					id='outlined-basic'
					label=''
					className='form-block__input'
					variant='outlined'
					{...register('count', { required: ' ' })}
				/>
				{errors.count && <p style={{ color: 'red' }}>{errors.count.message}</p>}
				{!imgUrl && (
					<label htmlFor='file-upload-create' className='custom-file-upload'>
						 
					</label>
				)}
				<input
					id='file-upload-create'
					ref={inputFileRef}
					type='file'
					style={{ display: 'none' }}
					onChange={handleFileChange}
				/>
			</div>
			{imgUrl && (
				<div className='custom-block-img'>
					<img
						className='form-block__img-upload'
						src={`${process.env.REACT_APP_SERVER_URL}/uploads/productImg/${imgUrl}`}
						alt=''
					/>
					{imgUrl && (
						<button className='settings__btn-delete' onClick={deleteImg}>
							 
						</button>
					)}
				</div>
			)}
			<div className='form-block__btns'>
				<button type='submit' className='button button--footer'>
					{nameButton}
				</button>
			</div>
		</form>
	)
}

export default AdminForm
